.notfound{
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.index{
  text-align: center;
  margin-top: 300px;
}

.content{
  justify-content: center;
  align-content: center;
}

#btn1{
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 250px;
  font-size: 25px;
  margin-top: 30px;
  margin: auto;
  flex-direction: row;
}
#btn2{
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 200px;
  font-size: 25px;
  margin: auto;
  flex-direction: column;
  margin-top: 20px;
}

label {
  color: white;
  padding: 0.5rem;
  font-family: sans-serif;
  border-radius: 0.3rem;
  cursor: pointer;
  margin-top: 1rem;
}

.searchbar{
  width: 50%;
  height: auto;
  margin: 0 auto;
  text-align: center;
}

.searchbar input{
  margin: 20px 0;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 20px;
}


.file-upload-btn {
  text-transform: uppercase;
  outline: none;
  background-color: #4aa1f3;
  font-weight: bold;
  padding: 8px 15px;
  margin-bottom: 5px;
  margin-left: 350px;
}
.upload-icon {
  width: 50px;
  height: 50px;
  background: url(./components/images/upload.png) no-repeat center center; 
  background-size: 100%;
  text-align: center;
  margin: 0 auto;
  padding-top: 30px;
}
.drop-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  width: 800px;
  height: 200px;
  border: 4px dashed #4aa1f3;
}
.drop-message {
  text-align: center;
  color: #4aa1f3;
  font-family: Arial;
  font-size: 20px;
}

.file-progressBar {
  width: 100%;
  border-radius: 5px; 
  overflow: hidden;
  display:inline-block;
  margin:0px 10px 5px 5px;
  vertical-align:top;
  flex: 6 !important;
  margin-left: 50px;
  box-sizing: content-box;
  width:calc(100% - 50px)
}

.file-display-container {
  position: fixed;
  width: 805px;
}

.file-type {
  display: inline-block!important;
  position: absolute;
  font-size: 12px;
  font-weight: 700;
  line-height: 13px;
  margin-top: 25px;
  padding: 0 4px;
  border-radius: 2px;
  box-shadow: 1px 1px 2px #abc;
  color: #fff;
  background: #0080c8;
  text-transform: uppercase;
}

.file-name {
  display: inline-block;
  vertical-align:top;
  margin-left: 50px;
  color: #4aa1f3;
}




